import {autoinject, computedFrom} from 'aurelia-framework';
import {Job} from '../../models/job';
import {Notifications} from '../../services/notifications';
import {JobService} from '../../services/data/job-service';
import {contains} from '../../utilities/equals';
import {sortBy} from '../../utilities/sort';
import { Configuration } from '../../services/config';
import { JobType } from '../../models/job-type';

const sortByNumber = sortBy('number', 'descending');

@autoinject
export class Search {
  allJobs:Job[] = [];
  search:string = '';
  hasFocus:boolean = false;
  isDesktop: boolean = true;

  constructor(jobService: JobService) {
    jobService.getAll()
      .then(jobs => this.allJobs = jobs)
      .catch(Notifications.error);
  }

  attached() {
    this.hasFocus = true;
    this.isDesktop = !Configuration.isMobile()
  }

  @computedFrom('search', 'allJobs')
  get jobs() {
    if(!this.search) return [];

    return this.allJobs.filter(j => {
      const prefix = j.job_type === JobType.SERVICE_CALL ? 'S' : 'P',
        numberDisplay = `${prefix}-${j.number}`;
      return contains(j.customer.name, this.search) ||
        contains(j.name, this.search) ||
        contains(numberDisplay, this.search);
    })
    .sort(sortByNumber);
  }
}
