import { autoinject } from "aurelia-framework";
import { DialogController, DialogService } from "aurelia-dialog";
import { SubJobOption, SubJobOptionDoc } from "../../models/sub-job-option";
import { SubJobOptionService } from "../../services/data/sub-job-service";
import { Notifications } from "../../services/notifications";
import { Prompt } from "../controls/prompt";

@autoinject()
export class EditSubJobOption {
  el: Element;
  option: SubJobOptionDoc;
  errors: string[] = [];

  constructor(
    private controller: DialogController,
    private service: SubJobOptionService,
    private dialogService: DialogService
  ) {}

  activate(option: SubJobOption | {} = {}) {
    this.option = new SubJobOptionDoc(option);
  }

  async save() {
    try {
      const result = await this.service.save(this.option);

      if (result.ok) {
        this.controller.ok(this.option);
      } else {
        this.errors = result.errors;
      }
    } catch (e) {
      Notifications.error(e);
    }
  }

  async delete() {
    try {
      this.dialogService
        .open({
          viewModel: Prompt,
          model: "Are you sure you want to delete this option?",
        })
        .whenClosed(async (promptResult) => {
          if (promptResult.wasCancelled) return;

          const result = await this.service.delete(this.option);

          if (result) {
            this.controller.ok(this.option);
          } else {
            this.errors = ["There was a problem deleting the option"];
          }
        });
    } catch (e) {
      Notifications.error(e);
    }
  }
}
