import { autoinject } from "aurelia-framework";
import { DialogController } from "aurelia-dialog";
import { JobService } from "../../services/data/job-service";
import { SubJobOptionService } from "../../services/data/sub-job-service";
import { JobDocument } from "../../models/job";
import { JobStatus } from "../../models/job-status";
import { guid } from "../../utilities/guid";
import { sortBy } from "../../utilities/sort";

const sortByValue = sortBy("value");

interface SelectableOption {
  value: string;
  name: string;
  checked: boolean;
}

@autoinject()
export class SubJobs {
  options: SelectableOption[] = [];
  job: JobDocument | null = null;

  constructor(
    private element: Element,
    private controller: DialogController,
    private jobService: JobService,
    private subJobService: SubJobOptionService
  ) {}

  async activate({ job }: { job: JobDocument }) {
    const jobs = await this.jobService.getAll(),
      options = await this.subJobService.getAll();

    this.options = options
      .filter((o) => !jobs.some((j) => j.number === `${job.number}${o.value}`))
      .sort(sortByValue)
      .map((o) => ({ ...o, checked: false }));
    this.job = job;
  }

  attached() {
    $(".ui.checkbox", this.element).checkbox();
  }

  detached() {
    $(".ui.checkbox").checkbox("destroy");
  }

  addOption() {
    this.options.push({ value: "", name: "", checked: true });

    window.setTimeout(() => {
      $(".ui.checkbox").checkbox("destroy").checkbox();
    }, 100);
  }

  save() {
    if (this.job) {
      this.options
        .filter((o) => o.checked && o.value)
        .forEach((o) => {
          const document = new JobDocument(this.job),
            json = document.toJSON();

          json._id = guid();
          delete json._rev;
          json.number = `${this.job.number}${o.value}`;
          json.status = JobStatus.PENDING;
          json.name = `${json.name}: ${o.name}`;

          this.jobService
            .save(json)
            .then((result) => {
              console.log(result);
            })
            .catch((e) => {
              console.log(e);
            });

          this.controller.close(true);
        });
    }
  }
}
