import { autoinject } from "aurelia-framework";
import { DialogService } from "aurelia-dialog";
import { EventAggregator, Subscription } from "aurelia-event-aggregator";
import * as $ from "jquery";
import { EditSubJobOption } from "./edit";
import { SubJobOption, SubJobOptionDoc } from "../../models/sub-job-option";
import { Database } from "../../services/data/db";
import { SubJobOptionService } from "../../services/data/sub-job-service";
import { Notifications } from "../../services/notifications";
import { ValidationResult } from "../../models/validation";
import { sortBy } from "../../utilities/sort";

const sortByValue = sortBy("value");

@autoinject
export class SubJobOptionList {
  el: Element;
  options: SubJobOption[];
  syncChangeSubscription: Subscription;

  constructor(
    private service: SubJobOptionService,
    private dialogService: DialogService,
    private events: EventAggregator
  ) {}

  async attached() {
    this.syncChangeSubscription = this.events.subscribe(
      Database.SyncChangeEvent,
      this.refresh.bind(this)
    );

    await this.refresh();
  }

  async refresh() {
    try {
      const options = await this.service.getAll();
      options.sort(sortByValue);
      this.options = options;
    } catch (e) {
      Notifications.error(e);
    }
  }

  new() {
    this.dialogService
      .open({ viewModel: EditSubJobOption })
      .whenClosed((result) => {
        if (result.wasCancelled) return;

        Notifications.success("Sub-Job Option saved successfully");
        this.refresh();
      })
      .catch(Notifications.error);
  }

  edit(model: SubJobOption) {
    this.dialogService
      .open({ viewModel: EditSubJobOption, model })
      .whenClosed((result) => {
        if (result.wasCancelled) return;

        const message = result.output
          ? "Sub-Job Option saved successfully"
          : "Sub-Job Option deleted successfully";
        Notifications.success(message);

        this.refresh();
      })
      .catch(Notifications.error);
  }
}
