import { CouchDoc, CouchDocBase } from "./couch-doc";
import { ValidationResult } from "./validation";

export interface SubJobOption extends CouchDoc {
  value: string;
  name: string;
}

export class SubJobOptionDoc
  extends CouchDocBase<SubJobOption>
  implements SubJobOption
{
  value: string;
  name: string;
  type: string = SubJobOptionDoc.SubJobOptionType;

  constructor(data: SubJobOption | {} = {}) {
    super(
      Object.assign(
        {
          _id: null,
          _rev: null,
          type: SubJobOptionDoc.SubJobOptionType,
          value: "",
          name: "",
        },
        data
      )
    );
  }

  validate(): ValidationResult {
    const result: ValidationResult = {
      ok: true,
      errors: [],
    };

    if (!this.value) {
      result.ok = false;
      result.errors.push("Please enter the Option value.");
    }

    if (!this.name) {
      result.ok = false;
      result.errors.push("Please enter the Option name.");
    }

    return result;
  }

  static SubJobOptionType: string = "sub-job-option";
}
