import { autoinject } from "aurelia-framework";
import { log } from "../log";
import { Configuration } from "../config";
import { UserInfo, storage_key } from "../auth";
import { equals } from "../../utilities/equals";
import { HttpClient } from "aurelia-fetch-client";

@autoinject()
export class UserService {
  database: PouchDB.Database;
  user_info: UserInfo;

  constructor(private config: Configuration, private httpClient: HttpClient) {
    const user_info: UserInfo = JSON.parse(localStorage[storage_key] || null);
    this.database = new PouchDB(`${this.config.remote_server}/_users`, {
      skip_setup: true,
      auth: { username: user_info.name, password: user_info.password },
    });
  }

  async getAll(): Promise<CouchUser[]> {
    try {
      const url = "user-filters/langendoen-users";
      const users = await this.database.query<CouchUser>(url, {
        include_docs: true,
      });
      return users.rows.map(({ doc }) => doc);
    } catch (e) {
      throw e;
    }
  }

  async getAllInRole(role: string): Promise<CouchUser[]> {
    const allUsers = await this.getAll(),
      inRole = allUsers.filter((u) => u.roles.some((r) => equals(r, role)));

    return inRole;
  }
}

export interface CouchUser {
  _id: string;
  _rev: string;
  name: string;
  type: string;
  roles: string[];
  phone?: string | null;
  email?: string | null;
  employeeId?: number | null;
}
